<template>
  <div style="height:100%;">
    <div class="topList">
      <span class="totalNumber">总计：800</span>
      <span class="preMonth">月购买桌卡：1200</span>
    </div>

    <div class="containerBox">
      <!-- 左边栏List -->
      <div class="list">
          <div class="left-list" id="picker">
              <div class="pickerList">
                 <picker :data='years' v-model='year1' @on-change='change'></picker>
             </div>
             <div class="pickerList">
                <picker :data='months' v-model='month1' @on-change='change'></picker>
            </div>
          </div>
      </div>
      <!-- 详情框 -->
      <div class="right_img">
           <!-- <group-title>{{year1}}{{month1}}</group-title> -->
        <div class="content">
            <div class="details after_">
                <p class="month">1</p>
                <p class="cards">桌卡：200</p>
                <p class="total">小计：400</p>
            </div>
            <div class="details  bgColor">
                <p class="month">2</p>
                <p class="cards">桌卡：200</p>
                <p class="total">小计：400</p>
            </div>
            <div class="details ">
                <p class="month">3</p>
                <p class="cards">桌卡：200</p>
                <p class="total">小计：400</p>
            </div>
            <div class="details ">
                <p class="month">4</p>
                <p class="cards">桌卡：200</p>
                <p class="total">小计：400</p>
            </div>

        </div>
      </div>

  </div>
  </div>
</template>
<script>
import {} from 'vux'
import axios from 'axios';
import { Cell, Group, Picker, GroupTitle, XButton } from 'vux'
import baseUtil from '../../baseUtil'
let years = []
let months = []
for (var i = 2016; i <= 2020; i++) {
  years.push({
    name: i,
    value: i + ''
  })
}
for (var j = 1; j <= 12; j++) {
  months.push({
    name: j + '月',
    value: j + ''
  })
}
export default {
  components: {
      Picker,
      GroupTitle,
      XButton,
      Cell,
      Group
},
data() {
    return {
        years: [years],
        year1: ['2017'],
        months:[months],
        month1:['8'],

    }
},
   methods:{
       change (value) {
           this
         console.log('new Value', value,this.year1[0],this.month1[0])
       },

   }
}

</script>
<style lang="css" scoped>
  .topList span{
    display: inline-block;
    float:right;
    font-size:.2rem;
    font-weight: 600;
    width:2rem;
    text-align: center;
    /* border:1px solid red; */
    border-radius: 14px;
    margin:.06rem .1rem;
    color:#fff;
    background:#F0EAEA;
  }
  .topList .totalNumber{
    margin-right:.2rem;
  }
  .containerBox{
    width:100%;
    height:90%;
    overflow-y:hidden;
  }
  .list {
    display: inline-block;
    width:26%;
    height:100%;
    background: url(../../images/left_bg.jpg) no-repeat ;
    background-size: 93% 100%;
     position: relative;
  }
  .left-list{
      width:92%;
      height:99.5%;
  }
  .list .pickerList{
    position: relative;
    height:45%;
    width:90%;
    margin: .15rem auto;
    border:2px solid #8E6AA8;
    border-radius: 20px;
    color:#fff;
    font-family: Microsoft YaHei;
    font-size:22px;
    overflow: hidden;
  }
  .list .vux-picker{
      width:100%;
      height: 100%;
      margin:0 auto;
  }
  #picker >>>.scroller-component{
    border-radius: 20px;
  }
  #picker >>> .vux-flex-row{
    height:100%;
  }
  #picker >>> .vux-picker-item{
    height:100%;
  }
  #picker >>> .vux-flexbox-item{
    height:100%;
  }
  #picker >>> .scroller-component{
    height:100%;
  }
  #picker >>> .scroller-item{
      font-size:28px;
      height:56px;
      line-height: 56px;
      color:#735D84;
  }
  #picker >>> .scroller-item-selected{
      color:#735D84;
       height:56px;
      font-size:40px;
  }
  #picker >>> .scroller-indicator{
      top:55px;
      height:56px;
      background-image: none;
      background:#fff;
      z-index:0;
  }
   #picker >>> .scroller-content{
       height:56px;
  }
   #picker >>> .scroller-mask{
      z-index:0;
      background-size: 100% 110px;
  }
  /* #picker>>> .vux-flex-row >>> .scroller-item{
    height:33.3%;
  } */
  #picker >>> .scroller-mask{
    display: none;
  }
  #picker >>> .scroller-component{
    background:#F0EAEA;
  }
  .top-content::-webkit-scrollbar{display: none}
  .right_img{
      display: inline-block;
      width:74%;
      height:100%;
      background: url(../../images/di1.png) no-repeat ;
      background-size: 100% 100%;
      float:right;
      color:#064381;
      position: relative;
  }
  .right_img  .content{
      width:96%;height:96%;
      margin:1% 2%;
      background: url(../../images/di2.png) no-repeat ;
      background-size: 100% 100%;
      overflow-y:auto;
  }
  .content .details{
      text-align: center;
      float:left;
      /* width:25%;
      height:33.3%; */
      width:20%;
      height:30%;
      margin:1.6% 2.5%;
  }
 .details .month{
     font-size:40px;
     font-weight: bold;
     line-height:40px;
 }
 .details{
     color:#79777C;
     font-size:.22rem;
 }
 .after_{
     color:#D68848;
 }

 .content .bgColor{
     background-color: #F5E4DA;
     color:#EF4F43;
     border-radius:10px;
 }

</style>



